import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class FreeBoiler extends React.Component {
  render() {
    return (
      <div>
        <p class="occupied">Someone is using boiler</p>
        <button onClick={() => this.props.onClick("Free")}>Free it</button>
      </div>
    );
  }
}

class OccupiyBoiler extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      time: 5
    };
  }

  handleChange = (event) => {
    this.setState({value: event.target.value});
  }

  render() {
    return (
      <div>
        <p  class="free">No one using boiler now</p>
        <p>input the time you want to use the boiler(unit: minute)</p>
        <input type="text" value={this.state.time} onChange={this.handleChange} />
        <button onClick={() => this.props.onClick("Occupied", this.state.time)}>Occupy it</button>
      </div>
    );
  }
}

class StateChanger extends React.Component {
  render() {
    if (this.props.state.match("Free")) {
      return (
        <OccupiyBoiler onClick={this.props.onClick}/>
      );
    } else {
      return (
        <FreeBoiler onClick={this.props.onClick}/>
      );
    }
  }
}

class State extends React.Component {
  render() {
    return (
    <p>boiler is {this.props.state}</p>
    );
  }
}

class Boiler extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      state: "Free",
      newState: ""
    };
    this.queryState();
  }

  queryState = () => {
    let url = "/state";
    fetch(url, {mode: 'cors'})
      .then(response => response.json())
      .then(data => this.setState({state: data.data.boilerState}));
  }

  postStateChange = (newState) => {
    let url = "/";
    if (newState.match("Free")) {
      url = url.concat("state/free");
    } else {
      url = url.concat("state/occupied");
    }
    fetch(url, {
      method: 'POST',
      mode: 'cors'
    })
      .then(response => response.json())
      .then(data => this.handleStateChangeResult(data.status));
  }

  handleStateChangeResult(result) {
    if (result === 1) {
      let newState = this.state.newState;
      this.setState({state: newState});
    } else {
      alert("change state failed, plase refresh and try again later");
    }
  }

  handleStateChange = (newState, time) => {
    alert(time);
    this.setState({newState: newState});
    this.postStateChange(newState);
  }

  render() {
    return (
      <div className="boiler">
        <div className="boiler-state">
          <State state={this.state.state}/>
        </div>
        <div className="State-Changer">
          <StateChanger onClick={this.handleStateChange} state={this.state.state} />
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Boiler />,
  document.getElementById('root')
);
